#result {
  font-size: 16px;
  border-radius: 10px;
  overflow: hidden;
  padding: 10px 15px;
  background: rgb(40, 44, 52);
}

#result .label-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

#result .label-container .copy-wrapper {
  text-align: right;
}

#result .label {
  color: rgb(121, 121, 121);
  text-transform: uppercase;
  font-size: 0.75em;
  margin-bottom: 5px;
}

#result pre {
  margin: 0;
  overflow-x: scroll;
}



