.fancy-input {
  background: white;
  padding: 10px 15px;
  border-radius: 10px;
  -webkit-box-shadow: rgba(0, 0, 0, 0.025) 0 20px 30px;
          box-shadow: rgba(0, 0, 0, 0.025) 0 20px 30px;
  -webkit-transition: 300ms -webkit-box-shadow;
  transition: 300ms -webkit-box-shadow;
  -o-transition: 300ms box-shadow;
  transition: 300ms box-shadow;
  transition: 300ms box-shadow, 300ms -webkit-box-shadow;
  display: block;
  cursor: text;
}

.fancy-input:focus-within {
  -webkit-box-shadow: rgba(0, 0, 0, 0.1) 0 20px 30px;
          box-shadow: rgba(0, 0, 0, 0.1) 0 20px 30px;
}

.fancy-input:focus-within .fancy-label {
  color: #4ea1ff;
}

.fancy-input .fancy-label {
  color: #cccccc;
  font-size: 0.75em;
  text-transform: uppercase;
  -webkit-transition: 300ms color;
  -o-transition: 300ms color;
  transition: 300ms color;
}

.fancy-input input {
  border: none;
  width: 100%;
  font-size: 1em;
  outline: none;
}



